//Global common items
#root {
  height: 100%;
}
.container {
  height: 100%;
}
body {
  margin: 0;
  padding: 0;
  height: 100%;
  font-family: "Poppins";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #333;
  overflow-x: hidden;
}
html {
  margin: 0;
  padding: 0;
  height: 100%;
}
.overlay {
  height: 100vh;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 999;
}

.overlay-without-color {
  height: 100vh;
  width: 100%;
  background-color: rgba(0, 0, 0, 0);
  z-index: 999;
}
.click-btn {
  padding: 15px 20px;
  box-shadow: 0 3px 10px #999;
  border-radius: $border_radius;
  text-align: center;
  cursor: pointer;
  transition: 0.3s linear;
  text-decoration: none;
  &.disabled{
    background-color: $gray_text;
    cursor: not-allowed;
  }
  &.primaryBtn {
    background-color: $primary_bg;
    color: #fff;
    &:hover {
      opacity: 0.7;
    }
  }
  &.secBtn {
    border: 1px solid $primary_bg;
    box-shadow: 0 0 5px $primary_bg;
    color: $primary_bg;
    &:hover {
      background-color: $primary_bg;
      color: #fff;
    }
  }
}

.primary-text {
  color: $primary_text;
}
.title {
  font-size: 35px;
  font-weight: 600;
  .title-inner {
    @include title_inner;
  }
}
.subtitle {
  font-size: 14px;
  letter-spacing: 2px;
  color: $gray_text;
  padding-bottom: 50px;
}

.section {
  padding: 40px $padding_lg 50px;
}
.swiper{
  padding: 20px 20px !important;
}
.swiper-button-next,
.swiper-button-prev {
  background-color: rgba(0, 0, 0, 0.3);
  border-radius: 50%;
  width: 30px !important;
  height: 30px !important;
  top: 55% !important;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  &::after {
    color: #fff;
    font-size: 16px !important;
    font-weight: 600;
  }
  &:hover {
    background-color: rgba(0, 0, 0, 0.5);
  }
}
@media (max-width: 1500px) {
  .section {
    padding: 30px $padding_sm 50px $padding_md;
  }
}

@media (max-width: 1200px) {
  .section {
    padding: 30px $padding_md 50px;
  }
  .title {
    font-size: 30px;
  }
  .subtitle {
    font-size: 12px;
  }
}

@media (max-width: 1100px) {
  .section {
    padding: 30px $padding_sm 50px $padding_md;
  }
}
@media (max-width: 1000px) {
  .section {
    padding: 30px $padding_md 50px;
  }
  .title {
    font-size: 26px;
  }
}

@media (max-width: 880px) {
  .section {
    padding: 30px $padding_md 50px;
  }
  .title {
    font-size: 20px;
  }
  .subtitle {
    font-size: 12px;
    padding-bottom: 25px;
  }
  .swiper-button-next,
  .swiper-button-prev {
    width: 25px !important;
    height: 25px !important;
    top: 57.5% !important;
    &::after {
      font-size: 14px !important;
    }
  }
}

@media (max-width: 550px) {
  .section {
    padding: 30px $padding_xs 50px;
  }
  .swiper-button-next,
  .swiper-button-prev {
    width: 20px !important;
    height: 20px !important;
    top: 55% !important;
  }
}
